.museum-of-art {
  margin-bottom: 70px;

  @include onTablet {
    margin-bottom: 88px;
  }

  @include onDextop {
    margin-bottom: 200px;
  }

  &__order {
    order: 1;

    @include onTablet {
      order: 0;
    }
  }

  &__picture {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: -1;
    height: 300px;
    background-image: url(../images/Natsionalnij-hudozhnij-muzej.png);
    background-position: center;
    background-size: 100% 100%;
    background-repeat: no-repeat;

    @include onTablet {
      position: relative;
      height: 100%;
      width: calc(100% + 39px);
    }

    @include onDextop {
      width: calc(100% + 55px);
    }
  }

  &__content {
    @include onTablet {
      padding-top: 127px;
    }
  }

  &__h1 {
    margin-bottom: 20px;

    @include onTablet {
      margin-bottom: 38px;
    }

    @include onDextop {
      margin-bottom: 89px;
    }
  }

  &__container2 {

    @include onDextop {
      display: flex;
      justify-content: space-between;
      margin-bottom: 99px;
    }
  }

  &__tickets {
    display: flex;
    box-sizing: border-box;
    border: 1px solid $green;
    height: 50px;
    margin-bottom: 19px;
    text-decoration: none;
    transition: ease 0.3s;

    &:hover {
      // background-color: $orange;
      border-color: $orange;
    }

    @include onTablet {
      width: 270px;
      height: 70px;
    }

    &--text {
      width: calc(100% - 49px);
      font-weight: 500;
      line-height: 50px;
      color: $black;
      text-align: center;
      @include onTablet {
        line-height: 70px;
      }
    }

    &--arrow {
      width: 49px;
      height: 100%;
      background-color: $green;
      background-image: url(../images/biw.png);
      background-position: center;
      background-repeat: no-repeat;
      transition: ease 0.3s;
      &:hover {
        background-color: $orange;
      }

      @include onTablet {
        width: 69px;
      }
    }
  }

  &__events {
    display: none;
    padding: 40px;
    width: min-content;
    transform: rotate(270deg) translateY(-70px) translateX(-18px);
    text-decoration: none;
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
    color: $gray;
    transition: e 0.3s;
    &::before {
      display: block;
      content: "";
      height: 1px;
      width: 70px;
      background-color: $green;
      transform: translateY(7px) translateX(-90px);
    }

    @include onDextop {
      display: block;
    }

    &:hover {
      color: $green;
    }
  }
}
