.current-events {
  &__title {
    margin-bottom: 40px;
  }

  &__exibition {
    overflow: hidden;
    @include onDextop {
      margin-bottom: 104px;
    }
  }

  &__lecture {
    @include onTablet {
      padding: 0 39px;
    }

    @include onDextop {
      padding: 0;
    }

    &-translate {
      padding-bottom: 1px;
      margin: 0 auto;
      background-color: #fff;
      transform: translateY(-50%);
      width: 280px;
      box-shadow: 0 2px 0 rgba(26, 90, 76, 0.25);

      @include onTablet {
        width: 570px;
      }
    }
  }

  &__picture {
    width: 100%;
    padding-bottom: 75%;
    background-position: center;
    background-size: cover;
    transition: transform 0.3s;

    &--1 {
      background-image: url(../images/pic1.png);
    }

    &--2 {
      background-image: url(../images/pic2.png);
    }

    &--3 {
      background-image: url(../images/pic3.png);
      padding-bottom: 65%;

      @include onTablet {
        padding-bottom: 55%;
      }

      @include onDextop {
        padding-bottom: 43%;
      }
    }
  }

  &__margin-bottom {
    margin-bottom: 20px;

    @include hoverPic;

    @include onTablet {
      margin-bottom: 40px;
    }

    @include onDextop {
      margin-bottom: 0;
    }
  }

  &__margin {
    margin: auto 0;
  }

  &__line {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include onTablet {
      justify-content: start;
    }

    &-lecture {
      padding: 24px 20px 0;

      @include onTablet {
        padding: 45px 55px 0;
      }

      @include onDextop {
        padding: 45px 100px 0;
      }
    }
  }

  &__name {
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: $gray;

    @include onTablet {
      font-size: 14px;
      line-height: 14px;
      margin-right: 30px;
    }

    &-lecture {
      @include onTablet {
        margin-right: 140px;
      }
    }
  }

  &__date {
    font-weight: 700;
    color: $green;

    @include onTablet {
      font-size: 14px;
      font-weight: 700;
      line-height: 21px;
    }
  }

  &__h3-cube {
    &::after {
      margin-left: 10px;
      display: inline-block;
      content: "";
      width: 10px;
      height: 10px;
      background-color: $orange;

      @include onTablet {
        width: 15px;
        height: 15px;
        margin-left: 15px;
      }
    }
  }

  &__h3 {
    text-align: center;
    margin: 20px 0;

    @include onTablet {
      text-align: left;
      margin: 50px 0 50px 55px;
    }

    @include onDextop {
      margin-left: 100px;
    }
  }

  &__text {
    max-width: 460px;
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 64px;
    }
  }
}
