.subscription {
  display: flex;
  background-image: url(../images/pic81.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 386px;

  @include onTablet {
    background-image: url(../images/pic8.png);
    height: 380px;
  }

  @include onDextop {
    height: 550px;
  }

  &__container {
    margin: auto;
    width: 280px;
    // height: 200px;

    @include onTablet {
      width: 460px;
    }

    @include onDextop {
      width: 570px;
    }
  }

  &__title {
    color: #fff;
    text-align: center;
    margin-bottom: 20px;

    @include onTablet {
      margin-bottom: 16px;
    }
  }

  &__text {
    text-align: center;
    margin-bottom: 50px;
    color: #fff;

    @include onTablet {
      margin-bottom: 64px;
    }

    @include onDextop {
      margin-bottom: 72px;
    }
  }

  &__form {
    display: flex;
  }

  &__email {
    box-sizing: border-box;
    width: 230px;
    height: 50px;
    padding: 0 0 0 28px;
    background-color: #fff;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    color: $black;
    border: 1px solid transparent;
    transition: border-color 0.3s;

    &:hover {
      border-color: $orange;
    }

    @include onTablet {
      height: 70px;
      width: 374px;
      padding-left: 32px;
      margin-right: 16px;
    }

    @include onDextop {
      width: 470px;
      margin-right: 30px;
    }

    &::placeholder {
      color: $gray;
      font-family: Montserrat, sans-serif;
      font-size: 14px;
      font-weight: 400;
    }
  }

  &__button {
    width: 50px;
    padding: 0;
    border: 0;
    height: 50px;
    background-color: $orange;
    background-image: url(../images/biw.png);
    background-position: center;
    background-repeat: no-repeat;

    @include hover;

    @include onTablet {
      width: 70px;
      height: 70px;
    }
  }
}
