h1 {
  font-family: Montserrat, sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  text-align: center;
  margin: 20px 0;
  color: #000;

  @include onTablet {
    font-size: 54px;
    line-height: 64px;
    text-align: left;
  }

  @include onDextop {
    font-size: 72px;
    line-height: 86px;
  }
}

h2 {
  font-family: Montserrat, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
  color: $black;
  text-align: center;

  @include onTablet {
    font-size: 48px;
    line-height: 48px;
    text-align: left;
  }
}

h3 {
  font-family: Montserrat, sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 28px;
  color: $black;

  @include onTablet {
    font-size: 36px;
    font-weight: 400;
    line-height: 36px;
  }
}

div {
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  color: #4e4e4e;
}
