.header {
  position: relative;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  margin-bottom: 300px;

  @include onTablet {
    margin-bottom: 0;
  }

  &__menu {
    position: absolute;
    top: 33px;
    left: 23px;
    height: 12px;
    width: 18px;
    background-image: url(../images/menu.png);
    background-position: center;
    @include hover;

    @include onTablet {
      top: 45px;
      left: 37px;
    }

    @include onDextop {
      left: 58px;
    }
  }

  &__logo {
    position: absolute;
    top: 9px;
    left: 50%;
    transform: translateX(-50%);

    @include onTablet {
      top: 15px;
      height: 72px;
      width: 65px;
      transform: translateX(calc(-50% + ((100vw - 224px) / 6)));
    }

    @include onDextop {
      transform: translateX(calc(-50% + ((100vw - 446px) / 11)));
    }
  }

  &__nav {
    padding-top: 32px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    transform: translateX(-100%);
    background-color: #fff;
    transition: transform 0.3s;

    &:target {
      transform: none;
    }
  }

  &__link {
    display: block;
    text-decoration: none;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: $black;
    margin-bottom: 24px;
    &:nth-last-child(5) {
      margin-bottom: 16px;
    }

    @include onDextop {
      font-size: 22px;
      margin-bottom: 30px;
    }

    transition: ease 0.3s;
    &:hover {
      color: $orange;
    }

    &--logo {
      height: 61px;
      width: 55px;
      background-image: url(../images/logo.png);
      background-position: center;
      background-size: cover;
      margin-bottom: 50px;

      @include hover;
    }
  }
}
