.grid {
  display: flex;
  flex-direction: column;

  @include onTablet {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;

    &__itemT {
      @for $start from 1 through 6 {
        @for $end from $start through 6 {
          &--#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }

  @include onDextop {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;

    &__itemD {
      @for $start from 1 through 12 {
        @for $end from $start through 12 {
          &--#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }

  &__gallary {
    gap: 20px;
    padding: 0 0 0 20px;
    flex-direction: row;
    margin-bottom: 16px;
    overflow: hidden;

    @include onTablet {
      display: flex;
      gap: 30px;
      padding: 0 0 0 34px;
      margin-bottom: 32px;
    }

    @include onDextop {
      display: grid;
      padding: 0 55px;
      margin-bottom: 0;
    }

    &--item {
      transition: ease 0.3s;
      width: calc(100% - 20px);
      height: 410px;
      flex-shrink: 0;
      overflow: hidden;

      @include onTablet {
        width: calc((100vw - 88px) / 2);
      }

      @include onDextop {
        width: 100%;
      }

      &:target {
        margin-right: 16px;
        margin-left: 16px;
        position: sticky;

        @include onTablet {
          margin-right: 16px;
          margin-left: 16px;
          transform: translateX(-30px);
        }
      }

      @include onDextop {
        height: 600px;
        margin-bottom: 30px;
      }

      &-3,
      &-4 {

        @include onDextop {
          height: 360px;
        }
      }
    }
  }
}
