.gallary {
  margin-bottom: 100px;

  @include onTablet {
    margin-bottom: 92px;
  }

  @include onDextop {
    margin-bottom: 200px;
  }

  &__title {
    margin-bottom: 40px;

    @include onTablet {
      margin-bottom: 48px;
    }

    @include onDextop {
      margin-bottom: 56px;
    }
  }

  &__picture {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    @include hoverPic;

    &-1 {
      background-image: url(../images/pic4.png);
    }

    &-2 {
      background-image: url(../images/pic5.png);
    }

    &-3 {
      background-image: url(../images/pic6.png);
    }

    &-4 {
      background-image: url(../images/pic7.png);
    }
  }

  &__slider {
    display: flex;
    gap: 22px;
    justify-content: center;

    @include onDextop {
      display: none;
    }
  }

  &__swich {
    display: block;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: $c-gray;

    &:hover {
      background-color: $green;
    }
    &:active {
      background-color: $orange;
    }
  }
}
