.footer {
  padding-top: 50px;
  padding-bottom: 60px;

  @include onTablet {
    padding-bottom: 36px;
  }

  &-grid {
    margin-bottom: 80px;

    @include onTablet {
      margin-bottom: 100px;
    }

    @include onDextop {
      margin-bottom: 87px;
    }

    @media (max-width: 767px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 20px;
  
      &__item--1 {
        grid-column: 1 / 2;
      }
  
      &__item--2 {
        grid-column: 2 / 3;
      }
  
      &__item--1-2 {
        grid-column: 1 / 3;
      }
    }
  }

  &__links {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    
    @include onTablet {
      flex-direction: column;
      align-items: start;
      // justify-content: start;
    }

  }

  &__logo {
    height: 72px;

    @include hover;

    @include onTablet {
      margin-bottom: 63px;
    }
  }

  &__socials {
    display: flex;
  }

  &__social {
    @include hover;

    &-facebook {
      margin-right: 25px;

      @include onTablet {
        margin-right: 35px;
      }
    }
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: $black;
    margin-bottom: 20px;
  }

  &__contact {
    display: block;
    text-decoration: none;
    color: #4e4e4e;
    font-weight: 400;
    line-height: 21px;

    &--header {
      color: #fff;
      transition: ease 0.3s;
      &:hover {
        color: $orange;
      }
    }
  }

  &__text-600 {
    color: $black;
    display: inline;
    font-weight: 600;
  }

  &__up {
    display: flex;
    justify-content: end;
    align-items: flex-end;
    height: 220px;
    @include hover;

    @include onTablet {
      height: 150px;
    }

    &-up {
      display: block;
      height: 50px;
      width: 50px;
      border: 1px solid $green;
      border-radius: 50%;
      background-image: url(../images/arrow.png);
      background-position: center;
      background-repeat: no-repeat;

      @include onTablet {
        display: none;
      }
    }
  }

  &__line {
    height: 1px;
    width: 100%;
    background-color: $green;
    margin-bottom: 20px;

    @include onTablet {
      margin-bottom: 64px;
    }

    @include onDextop {
      width: 1px;
      height: 260px;
      margin: 0 auto;
    }
  }

  &__link {
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 14px;
    color: $black;
    text-decoration: none;
    margin-bottom: 20px;
    @include hover;
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 10px;
    text-align: center;

    @include onTablet {
      flex-direction: row;
      justify-content: space-between;
    }

    &-text {
      font-size: 12px;
      line-height: 17px;
      color: $black;
    }
  }
}
